.Hint {
  position: absolute;
  padding: 10px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 4px;
  color: white;
  font-size: 12px; }
  .Hint:before {
    width: 0;
    height: 0;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-bottom: 7.8px solid rgba(0, 0, 0, 0.7);
    content: '';
    position: absolute;
    top: -8px;
    left: 40px; }



